import React from "react";
import { Link } from "react-router-dom";
import "./TopLinks.css";

export default function TopLinks() {
  return (
    <div id="links">
      <div id="soloLinks">
        <Link
          className="linkStyle"
          style={{ fontSize: 20, color: "white" }}
          to="/"
        >
          Add Room
        </Link>
        <Link
          className="linkStyle"
          style={{ fontSize: 20, color: "white" }}
          to="/Room"
        >
          Room's List
        </Link>
        <Link
          className="linkStyle"
          style={{ fontSize: 20, color: "white" }}
          to="/Loby"
        >
          Loby
        </Link>
      </div>
    </div>
  );
}
