import React, {useState} from 'react';
import Room from './components/Room'
import './App.css';
import {HashRouter as Router, Switch, Route} from 'react-router-dom';
import Header from './components/Header'
import InsideRoom from './components/InsideRoom'
import Loby from  './components/Loby'
import Welcome from './components/Welcome'
import TopLinks from './components/TopLinks'

import NewInput from './components/NewInput'





function App() {
  const [rooms,setRooms]= useState([])
  const [products,setProducts]= useState([{}])
  const [soloRoom,setSoloRoom]=useState([{}])
  const [updates,setUpdates]=useState([])
  const [flag2,setFlag2]=useState(false)
  
 
const flagchange=()=>{
  setFlag2(!flag2)}

 
  const addnewpost=(t,i)=>{
    if(!t || !i){
      return;
    }
    setRooms([...rooms,{title:t,info:i, flag:1}])
    
  }

  const addUpdate=(t)=>{
    
    if(!t){
      return;
    }
    
    setUpdates([...updates,{tool:t}])
   
    
    
  }

  const addnewprops= (name,prop,i)=>{
    if(prop){
      setProducts([...products,{title:name,tool:prop,info:i}])
      // setToolList(...toolList,{tool:prop})
      setUpdates([...updates,{tool:prop}])
      setRooms([...rooms,{tool:prop}])
      // setSoloRoom([...soloRoom,{tool:prop}])
      setFlag2(true)
    
    }
    
  }

  // const updateRoom=(tools)=>{
    
  //   if(!tools){
  //     return;
  //   }
  //   setSoloRoom([...soloRoom,{tool:tools}])

  // }
    
   
  
  
  return (
    <div className="App">
      
      <Header /><br/>
      <Router>
        <TopLinks/>
             
          
      <Switch>
          <Route exact path='/' component={()=>{return <div style={{textAlign:"center", alignItems:"center", justifyContent:"center", objectPosition:"center"}} className="background">
       {/* <NewWelcome/>        */}
      <Welcome />
      </div>}} />   
          <Route exact path='/AddRoom' component={()=>{return <div className="background">
      
      <NewInput add={addnewpost}/>
      
      {/* <AddRoom add={addnewpost} /> */}
      </div>}} />

      <Route exact path='/Room' component={()=>{return <div>
          <div className="backgroundRooms" style={{textAlign:"center", alignItems:"center", display:"flex", justifyContent:"center"}}>
          {rooms.map((item, i) => {
              return  <Room key={i} flag={item.flag} name={item.title} info={item.info} index={item.id} add={addnewprops} flag2={flagchange}/>
            })}
                     
      </div>
      
          
      
      </div>}} />
      

      <Route exact path='/Loby' component={()=>{return <div className="background5" style={{textAlign:"center", alignItems:"center",display:"flex", justifyContent:"center"}}>
        {/* <Loby name="itay" tool="lamp" info="red" /> */}
        {/* <Loby name={products.map(item=>{return item.name})} tool={products.map(item=>{return item.tool})} info={products.map(item=>{return item.info})} /> */}
        {products.map((r,i)=>{return<Loby key={i} list={updates} newTool={soloRoom.map(n=>{return n.tool})} addnew={addUpdate} name={r.title} tool={r.tool} info={r.info} flag={flag2}/>})}
      </div>}} />

      {/* <Route exact path='/InsideRoom' component={()=>{return <div><InsideRoom tool={soloRoom.map(tool=>{return tool})}/></div>}}/> */}
      <Route exact path='/InsideRoom' component={()=>{return <InsideRoom tool={updates.map(tool=>{return tool.tool})}/>}}/>
      </Switch>
      </Router>
     
     
    </div>
  );
}

export default App;



