import React, {useState} from 'react';



export default function AddProduct(props) {
    const [tool,setTool]=useState([]);
    
    
    const btnAdd=()=>{
        // const index="";
        if(tool!=="" && tool!=="Select new props")
        {

        props.add(props.name,tool,props.info)
    }
}
    
    

    const show=()=>{
            
            if(props.flag===1 && props.name){ return <div>
            <select style={{padding:10, borderColor:"blue", border:"dotted", textAlign:"center", objectPosition:"center"}} onClick={(e)=>{setTool(e.target.value)}}><option>Select new props</option>
            <option value="lamp">Lamp</option>
            <option value="air-condition">Air condition</option>
            <option value="stereo" >Stereo</option>
            <option value="boiler" >Boiler</option>
            </select><br/>
            
             <br/>
             <button style={{backgroundColor:"rgb(74, 143, 6)",fontSize:"14px", color:"azure", borderColor:"darkgreen", borderRadius:"5px", transition:"0.8s", cursor:"pointer", }} onClick={btnAdd} >Add New Room</button>
             </div>
            }
        }
   
    return (
        <div>
         
             {show()}
           
        </div>
    )

    
}
