import React from 'react';
import './Room.css'
// import {HashRouter as Router, Switch, Route, Link} from 'react-router-dom';

import AddProduct from './AddProduct';

export default function Room(props) {
    
    // setTitle({title:props.name});
    
    const showIfNotEmpty=()=>{
        
        if(props.name && props.flag===1){
            
            
            return <div className="squareRoom" style={{backgroundColor:props.info}}>
            
            <h2>{props.name}</h2>
            
            
            
                
            <AddProduct flag={1} add={props.add} name={props.name} flag2={props.flag2} info={props.info}/>
            
                <br/>
                
        </div>
        }
        
        
        
        
    }
    
    return (
        <div style={{justifyContent:"center"}}>
      
            
             {showIfNotEmpty()}


          
         </div>
        
    )
}

