import React, { useState } from "react";
import "./Newinput.css";

export default function NewInput(props) {
  const [title, setTitle] = useState("");
  const [info, setInfo] = useState("");

  const re = () => {
    props.add(title, info);
    window.location="http://localhost:3000/#/Room";
  };
  return (
    <div className="container">
      <div className="login-box">
        <h1>Enter name and color for the room</h1>

        <div className="textbox">
          {/* <i className="fas fa-user"></i> */}
          <i className="fa fa-home"></i>
          <input
            type="text"
            onChange={e => {
              setTitle(e.target.value);
            }}
            placeholder="Room's name"
          ></input>
        </div>
        <div className="textbox">
          <i className="fas fa-photo"></i>

          <input
            type="text"
            onChange={e => {
              setInfo(e.target.value);
            }}
            placeholder="Room's color"
          ></input>
        </div>
        {/* <input type="button" className="btn" value="Sign in"></input> */}
        <button className="btn" onClick={re}>
          Create Room
        </button>
      </div>
    </div>
  );
}
