import React, { useState } from "react";
// import lampPic from './lamp.jpg'
// import stereo from './speakers.jpg'
// import air from './air.jpg'
// import boiler from './boiler.png'
import "./Loby.css";
// import { Link } from "react-router-dom";

export default function Loby(props) {
  const [tool, setTool] = useState([props.tool]);
  const [roomProps, setRoomProps] = useState([{ tool: props.tool }]);
  const [updateRoom, setUpdateRoom] = useState([roomProps]);

  // const findPic=(tool)=>{
  //     if (tool.indexOf("lamp")!==-1){
  //         return lampPic
  //     }
  //     else if (tool.indexOf("stereo")!==-1){
  //         return stereo
  //     }
  //     else if (tool.indexOf("boiler")!==-1){
  //         return boiler
  //     }
  //     else {
  //         return air
  //     }

  // }
  const emptyTools = () => {
    setRoomProps("");
    return;
  };
  const showTools = obj => {
    if (!obj) return "";

    return (
      <div>
        {obj.map((tool, i) => {
          if (
            tool.tool === "lamp" ||
            tool.tool === "air-condition" ||
            tool.tool === "boiler" ||
            tool.tool === "stereo"
          )
            return (
              <div key={i} style={{ display: "block" }}>
                <table id="tableTools">
                  <tbody>
                    <tr>
                      <td>{tool.tool}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            );
        })}
      </div>
    );
  };
  const addAnother = () => {
    if (tool !== "" && tool !== "Select new props") {
      setRoomProps([...roomProps, { tool: tool }]);
      setUpdateRoom([...updateRoom, { tool: tool }]);
    }
  };

  // const updateNewTool=()=>{
  //     props.addnew(updateRoom)
  // }

  const checkValue = () => {
    if (props.tool && props.tool !== "Select new props" && props.flag) {
      return (
        <div
          className="square-room"
          style={{
            textAlign: "center",
            alignItems: "center",
            justifyContent: "center",
            backgroundColor: props.info,
            height: 400,
            width: 150,
            marginLeft: 20
          }}
        >
          <h3 className="roomName" style={{ textDecoration: "underLine" }}>
            Room's Name:
          </h3>
          <h4 className="propsName">{props.name}</h4>

          {/* <h2>{props.r}</h2> */}
          <h3 className="roomItem" style={{ textDecoration: "underLine" }}>
            Room's Items
          </h3>

          <h4 id="choise" className="propsItem">
            {showTools(roomProps)}
          </h4>
          <p id="answer"></p>
          <select
            className="select-box"
            style={{
              padding: 5,
              borderColor: "blue",
              border: "dotted",
              textAlign: "center",
              objectPosition: "center"
            }}
            onClick={e => {
              setTool(e.target.value);
            }}
          >
            <option>Select new props</option>
            <option value="lamp">Lamp</option>
            <option value="air-condition">Air condition</option>
            <option value="stereo">Stereo</option>
            <option value="boiler">Boiler</option>
          </select>
          <br />
          <button className="btnSelect" onClick={addAnother}>
            Add
          </button>
          <br />
          <button className="btnSelect" onClick={emptyTools}>
            Clear
          </button>
          {/* <button onClick={()=>{props.update22(roomProps)}}>go</button> */}

          {/* <button onClick={updateNewTool}>Save</button> */}
          {/* <Link
            className="roomLink"
            style={{ fontSize: 20, color: "white" }}
            to="/InsideRoom"
          >
            inside
          </Link> */}
          {/* <img style={{width:100}} src={findPic(props.tool)} alt="pic"></img> */}
        </div>
      );
    }
  };

  const alwaysUpdate = () => {
    props.addnew(roomProps);
  };
  return (
    <div
      className="background5"
      style={{
        height: 80,
        textAlign: "center",
        alignItems: "center",
        justifyContent: "center",
        padding: 15
      }}
    >
      {checkValue()}
    </div>
  );
}
